import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import { withStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import Picture from "../../zoomInt.png";

const useStyles = makeStyles({
  root: {
    width: 300,
    height: 320,
    margin: "20px",
    borderRadius: "20px",
    "&:hover": {
      boxShadow: "0px 0px 40px 5px #00f2ff",
    },
  },
  media: {
    height: 200,
  },
});
const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

function ZoomInt() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={handleClickOpen}>
        <CardMedia
          className={classes.media}
          image={Picture}
          title="Zoom Integration"
        />
        <CardContent>
          <Typography gutterBottom variant="h6" component="h2">
            Zoom Integration
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            Node.js/ Express/ Axios/ JWT/ HTML5 / CSS
          </Typography>
        </CardContent>
      </CardActionArea>
      <Dialog onClose={handleClose} aria-labelledby="BS-site" open={open}>
        <DialogTitle id="BS-site" onClose={handleClose}>
          Zoom Integration (Zoom API/ Zoom SDK)
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            Node.js/ Express/ Axios/ JWT/ HTML5 / CSS
          </Typography>
          <Typography gutterBottom>
            <ul>
              <li>
                The layout is fully customizable according to client's needs.
              </li>
              <li>
                Using Zoom API to register participant's details to Zoom
                webinar.
              </li>
              <li>
                Registrant will receive a confirmation from Zoom directly after
                registration.
              </li>
            </ul>
          </Typography>
          <Typography gutterBottom>Screenshots:</Typography>
          <div style={{ textAlign: "center" }}>
            <img
              src={Picture}
              style={{ width: "500px" }}
              alt="Zoom Customised Registration Form screenshot"
            />

            <Typography gutterBottom>
              <Link
                href="https://zoomintegration-313407.df.r.appspot.com/"
                alt="Customized Zoom Registration Form"
                target="_blank"
                rel="noreferrer"
                color={"inherit"}
              >
                &#8618;Link to demo
              </Link>
            </Typography>
          </div>
        </DialogContent>
      </Dialog>
    </Card>
  );
}

export default ZoomInt;
