import { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import { withStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import MuiDialogContent from "@material-ui/core/DialogContent";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Link from "@material-ui/core/Link";
import Picture from "../../BS_site.png";
import Screenshot1 from "../../BS_site_1.png";
import Screenshot2 from "../../BS_site_2.png";

const useStyles = makeStyles({
  root: {
    width: 300,
    height: 320,
    margin: "20px",
    borderRadius: "20px",
    "&:hover": {
      boxShadow: "0px 0px 40px 5px #00f2ff",
    },
  },
  media: {
    height: 200,
  },
});
const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton
          aria-label="close"
          className={classes.closeButton}
          onClick={onClose}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

function BSSite() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={handleClickOpen}>
        <CardMedia
          className={classes.media}
          image={Picture}
          title="Best Soliution Website"
        />
        <CardContent>
          <Typography gutterBottom variant="h6" component="h2">
            Best Solution Company Ltd. Website
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            HTML5/ CSS/ JavaScript/ JQuery
          </Typography>
        </CardContent>
      </CardActionArea>
      <Dialog onClose={handleClose} aria-labelledby="BS-site" open={open}>
        <DialogTitle id="BS-site" onClose={handleClose}>
          Best Solution Company Ltd. Website
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>HTML5/ CSS/ JavaScript/ JQuery</Typography>
          <Typography gutterBottom>Screenshots:</Typography>
          <div style={{ textAlign: "center" }}>
            <img
              src={Picture}
              style={{ width: "500px" }}
              alt="BS site screenshot"
            />
            <img
              src={Screenshot1}
              style={{ width: "500px" }}
              alt="BS site screenshot"
            />
            <img
              src={Screenshot2}
              style={{ width: "500px" }}
              alt="BS site screenshot"
            />
            <Typography gutterBottom>
              <Link
                href="https://bestsolution.com.hk/"
                alt="Best Solution Company Limited Webiste"
                target="_blank"
                rel="noreferrer"
                color={"inherit"}
              >
                &#8618;Link to website
              </Link>
            </Typography>
          </div>
        </DialogContent>
      </Dialog>
    </Card>
  );
}

export default BSSite;
